.card {
  padding: 10px 20px;
  flex-grow: 1;

  height: 48px;
  text-transform: capitalize;
  background-color: #ffffffe6;
  border-radius: 2px;
  box-shadow:
    0 2px 2px #0000003d,
    0 0 2px #0000001f;
}

.active {
  max-width: 241px;
  height: 52px;
}

.card.active .dayText {
  font-weight: var(--fw-medium);
}